<template>
  <div class="login">
    <div class="con">
      <h3>欢迎登录</h3>
      <el-input placeholder="请输入用户名" v-model="user.username"></el-input>
      <el-input type="password" placeholder="请输入密码" v-model="user.password"></el-input>
      <div class="btn">
        <el-button type="primary" @click="login">登录</el-button>
      </div>
    </div>
  </div>
</template>

<script>
import {requestLogin} from '../../utils/request'
import { successAlert, errorAlert } from '../../utils/alert'
import {mapActions} from 'vuex'
export default {
  data(){
    return {
      user:{
        username:'',
        password:'',
      }
    }
  },
  methods:{
    ...mapActions({
      "requestUser":"userActions"
    }),
    login(){
      // 方式二
      //发起登录请求时,将用户信息存入到sessionStorage中
      if(this.user.username !== '' || this.user.password !== ''){
        // 发起登录请求
        requestLogin(this.user).then(res=>{
          successAlert(res.data.msg);
          // 将用户信息存入到store中
          this.requestUser(res.data.list);
          this.$router.push('/')
        })
      }else{
        errorAlert('用户名或者密码不能为空');
        return;
      }



      // 方式一
      // 存储到localStorage中
      // 发起登录请求
      // if(this.user.username !== '' && this.user.password !== ''){
      //   //发起登录请求
      //   requestLogin(this.user).then(res=>{
      //     successAlert(res.data.msg);
      //     // 将用户信息存入到localStorage中,每次进来token的值都不一样
      //     if(localStorage.getItem('user') !== null){
      //       localStorage.removeItem('user')
      //     }
      //     localStorage.setItem('user',JSON.stringify(res.data.list));
      //     this.$router.push('/')

      //   })
      // }else{
      //   alert('用户名或者密码不能为空');
      //   return;
      // }
    }
  }
}
</script>

<style scoped>
.login{
  width:100vw;
  height: 100vh;
  background: linear-gradient(#563443,#2F3D60);
  position:fixed;
  left:0;
  top:0;
}
.con{
  width:400px;
  height: 300px;
  background: white;
  position:absolute;
  left:50%;
  top:50%;
  transform: translate(-50%,-50%);
}
h3{
  text-align: center;
  margin:20px;
}
.el-input,.el-button{
  width:90%;
  margin: 20px;
}
</style>



